import { useState, useRef, useEffect } from 'react';
import { useSelector,  useDispatch } from "react-redux"
import { pickItem } from '../../actions/index';

import '../../css/productUnit.css';

function productUnitRestaurantMap({item, map, reverse}) {
    const fid = useRef();
    const ftitle = useRef();

    const [ likeItems, setLikeItems ] = useState([]);
    const dispatch = useDispatch();

    function like() {
        console.log("클릭");
        const productId = fid.current.id;
        const productTitle = ftitle.current.innerHTML;
        dispatch(pickItem({productId,productTitle}));
        const newLike = {
            id : productId,
            title : productTitle,
        };
        setLikeItems([newLike,...likeItems]);
        console.log(likeItems);
        localStorage.setItem('likeState2', JSON.stringify(likeItems));
        console.log(localStorage.getItem('likeState2'));
    }

    const items =  1 ;
    return (
        <div className="productUnit-frame">
            <div className="productUnit-img-restaurant">
            {map}
            </div>
            <div className="productUnit-contents">
                <div className="productUnit-contents-tit">
                    <p className="unit-tit text-style-13" ref={ftitle}>서브타이틀(맛집)</p>
                    <p className="unit-tit text-style-24">만선식당</p>
                    <div className="productUnit-contents-like">
                        <div id={items} ref={fid} className="heartNotActive" onClick={(event) => {
                                                                            reverse(event);
                                                                            like(event);
                                                                        }}></div>
                        <h1 className="unit-tit text-style-13">111</h1>
                    </div>
                </div>
                <div className="productUnit-contents-address">
                    <h1 className="unit-tit text-style-13">제주특별자치도 서귀포시 대정읍 하모항구로 44</h1>
                </div>
                <div className="productUnit-contents-rating">
                    <div className="star-rating-16">
                        <i class="ic-star is-active"></i>
                        <i class="ic-star is-active"></i>
                        <i class="ic-star is-active"></i>
                        <i class="ic-star is-active"></i>
                        <i class="ic-star is-active"></i>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default productUnitRestaurantMap;