import React from "react";
import Slider from "react-slick";
import ProductUnit from './cardUI/productUnitActivity';
import "../css/slider.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styled from 'styled-components';
import NextArrow from './slider/nextArrow';


const StyledSlider = styled(Slider)`
margin-bottom: -100px;
height: 75vh;
  .slider {
    display:block;
    height: 45vh;
    margin-bottom:200px;
  }
`;

export default function SimpleSlider({data, setCart, closeCart, likeModalClose, store, reverse}) {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      nextArrow: <NextArrow />,
      slidesToShow: 4,
      slidesToScroll: 4,
      cssEase: "linear",
      responsive: [ // 반응형 웹 구현 옵션
					{  
						breakpoint: 960, //화면 사이즈 960px
						settings: {
							slidesToShow:3 
						} 
					},
					{ 
						breakpoint: 768, //화면 사이즈 768px
						settings: {	
							slidesToShow:2 
            } 
            }]
        }
    return (
      <div>
        <StyledSlider {...settings}>
        <div className="slider">
            <ProductUnit item={data} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse}/>
        </div>
        <div className="slider">
              <ProductUnit item={data} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse}/>
        </div>
        <div className="slider">
              <ProductUnit item={data} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse}/>
        </div>
        <div className="slider">
              <ProductUnit item={data} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse}/>
        </div>
        <div className="slider">
              <ProductUnit item={data} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse}/>
        </div>
        <div className="slider">
              <ProductUnit item={data} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse}/>
        </div>
        </StyledSlider>
      </div>
    );
  }
