import { useState } from 'react';
import { Link } from "react-router-dom";
import Modal from "../modals/modal.jsx"; 
import styled from 'styled-components';

const Td = styled.td`
display:block;
padding:0 5px;
align-items:center;
white-space:nowrap;
width:200px;
height:100%;
overflow:hidden;
text-overflow:ellipsis;
`;

const Tdd = styled.td`
display:block;
padding-left:30px;
align-items:center;
white-space:nowrap;
width:340px;
height:100%;
overflow:hidden;
text-overflow:ellipsis;
`;
const posts = ({ posts, loading, reverse }) => {
    
    const [ modalOpen, setModalOpen] = useState();

    const modalClose = () => {
      setModalOpen(!modalOpen);
    }
        return(
        <>
        { loading && <div> loading ... </div> }
            {
                posts.map((post,index) => (
                        <tr key={index}>
                        <td>{post.id}</td>
                        <td><Tdd><Link to="/detail">{post.title}</Link></Tdd></td>
                        <td><Td>{post.region}</Td></td>
                        <td>{post.price}</td>
                        <td>{post.regdate}</td>
                        <td>{post.hit}</td>
                        <td><div id={index} className="heartNotActive" onClick={reverse}></div></td>
                        <td><div className="tag-outline" onClick={modalClose}>예약하기</div>
                        {
                        modalOpen && <Modal
                        modalClose={modalClose}
                        id={post.id}
                        title={post.title}
                        region={post.region}
                        price={post.price}
                        />
                        }</td>
                        </tr>
                ))
            }
        </>
    );
};

export default posts;